/* 右侧 */
.ypy-bar {
  position: fixed;
  _position: absolute;
  width: 0;
  overflow: visible;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 999999;
  background: transparent;
  .ypy-outer {
    visibility:  visible; /* 测试 */
    position: fixed;
    _position: absolute;
    width: 270px;
    top: 0;
    right: -270px;
    z-index: 999999;
    color: #fff;
    font-family: "Microsoft Yahei","\5fae\8f6f\96c5\9ed1","\5b8b\4f53";
    _overflow-y: hidden;
  }
  .ypy-tabs {
    position: absolute;
    z-index: 999999;
    top: 0;
    left: 0px;
    width: 35px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    .ypy-tabs-mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 35px;
      background-color: #000;
    }
  }
}
.ypy-mbar-tab {
  position: relative;
  left: 0;
